.about {
  padding-top: 125px;
  background-color: $contrastBgColor;

  background-image: url(../images/about/milk-mob.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom 60px;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/about/milk-mob2x.png);
  }

  @media screen and(min-width:$tablet) {
    background-image: none;
  }

  @media screen and(min-width:$desktop) {
    background-image: url(../images/about/milk-desktop.png);
    background-position: center bottom;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/about/milk-desktop2x.png);
    }
  }

  @media screen and(min-width:1970px) {
    background-size: 100% 100%;
  }

  // @media screen and(max-width:767px) {
