  //   background-repeat: no-repeat;
  //   background-size: contain;
  //   background-position: 50% 125%;

  //   @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  //     background-image: url(../images/about/milk-desktop2x.png);
  //   }
  // }
}

.about-pre__title {
  margin-bottom: 15px;
  text-align: center;
}

.about__title {
  margin-bottom: 26px;
  text-align: center;

  @media screen and(min-width:$tablet) {
    margin-bottom: 50px;
  }

  @media screen and(min-width:$desktop) {
    margin-bottom: 55px;
  }
}

.about-container {
  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: center;
  }
}

.about-content {
  position: relative;
  @media screen and (min-width: $tablet) {
    width: 50%;

    padding-top: 50px;
    &:first-of-type {
      margin-left: 17px;
    }
  }

  @media screen and (max-width: 767px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media screen and (min-width: $desktop) {
    width: 40%;
    padding-top: 56px;

    &:first-of-type {
      margin-left: 74px;
    }
  }
}

.about-content__text {
  @include font('DM Sans', 500, 12px, 1.62);
  color: $greyColor;

  &:first-child {
    color: #000000;
    margin-bottom: 35px;
  }

  &:nth-last-child(-n + 3) {
    margin-bottom: 17px;
  }

  @media screen and(min-width:$tablet) {
    @include font('DM Sans', 500, 14px, 1.76);

    &:nth-last-child(-n + 2) {
      margin-bottom: 60px;
    }
  }

  @media screen and(min-width:$desktop) {
    font-size: 16px;
    letter-spacing: 0.04em;

    &:nth-last-child(-n + 3) {
      font-size: 16px;
      line-height: 1.94;
    }

    &:nth-last-child(-n + 2) {
      margin-bottom: 45px;
    }
  }
}

.overlay-text {
  @extend .overlay-text;

  .about-button:active & {
    opacity: 1;
  }

  .about-button:focus & {
    opacity: 1;
  }
}
